<template>
  <div class="contact content-container">
    <h1 class="page-title">Contact Me</h1>
    
    <div class="tabs-container">
      <button 
        v-for="tab in tabs" 
        :key="tab.key"
        @click="currentTab = tab.key"
        :class="['tab-button', { active: currentTab === tab.key }]"
      >
        {{ tab.label }}
      </button>
    </div>

    <div class="controls">
      <div class="view-toggle">
        <span>View:</span>
        <button 
          v-for="view in viewOptions" 
          :key="view.value"
          @click="currentView = view.value"
          :class="{ active: currentView === view.value }"
        >
          <i :class="['fas', view.icon]"></i>
        </button>
      </div>
    </div>

    <div class="contact-list">
      <div v-for="contact in currentContacts" :key="contact.name" class="contact-item">
        <div v-if="currentView === 'compact'" class="contact-compact">
          <img :src="require(`@/assets/icons/${contact.icon}`)" :alt="contact.name + ' icon'" class="contact-icon">
          <span class="contact-name">{{ contact.title }}</span>
          <img :src="getContactImage(contact.name)" :alt="contact.name + ' contact'" class="contact-image">
          <i v-if="contact.copyable" class="fas fa-copy copy-icon" @click="copyToClipboard(contact.value)"></i>
        </div>
        <div v-else-if="currentView === 'grid'" class="contact-grid">
          <h3>{{ contact.title }}</h3>
          <img :src="contact.qrCode" :alt="contact.name + ' QR Code'" class="qr-code-image">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'

export default {
  name: 'Contact',
  setup() {
    const currentTab = ref('communication')
    const currentView = ref('compact')

    const tabs = [
      { key: 'communication', label: 'Communication' },
      { key: 'messaging', label: 'Messaging' },
      { key: 'payments', label: 'Payments' }
    ]

    const viewOptions = [
      { value: 'compact', icon: 'fa-bars' },
      { value: 'grid', icon: 'fa-th-large' }
    ]

    const contacts = {
      communication: [
        { 
          name: 'email', 
          title: 'Email',
          icon: 'email.svg',
          qrCode: '/images/contact/email-qr.png',
          value: 'example@email.com',
          copyable: false
        },
        { 
          name: 'linkedin', 
          title: 'LinkedIn',
          icon: 'linkedin.svg',
          qrCode: '/images/contact/linkedin-qr.svg',
          value: 'linkedin.com/in/yourprofile',
          copyable: false
        },
        { 
          name: 'github', 
          title: 'GitHub',
          icon: 'github.svg',
          qrCode: '/images/contact/github-qr.png',
          value: 'github.com/yourusername',
          copyable: false
        },
        { 
          name: 'gitlab', 
          title: 'GitLab',
          icon: 'gitlab.svg',
          qrCode: '/images/contact/gitlab-qr.png',
          value: 'gitlab.com/yourusername',
          copyable: false
        }
      ],
      messaging: [
        { 
          name: 'session', 
          title: 'Session',
          icon: 'session.svg',
          qrCode: '/images/contact/session-qr.svg',
          value: '05ab3cf5d7f9f5f0...',
          copyable: true
        },
        { 
          name: 'signal', 
          title: 'Signal',
          icon: 'signal.svg',
          qrCode: '/images/contact/signal-qr.svg',
          value: '+1234567890',
          copyable: false
        },
        { 
          name: 'whatsapp', 
          title: 'WhatsApp',
          icon: 'whatsapp.svg',
          qrCode: '/images/contact/whatsapp-qr.svg',
          value: '+1234567890',
          copyable: false
        },
        { 
          name: 'telegram', 
          title: 'Telegram',
          icon: 'telegram.svg',
          qrCode: '/images/contact/telegram-qr.svg',
          value: '@username',
          copyable: false
        }
      ],
      payments: [
        { 
          name: 'monero', 
          title: 'Monero',
          icon: 'monero.svg',
          qrCode: '/images/contact/monero-qr.png',
          value: '4AB...XyZ',
          copyable: true
        }
      ]
    }

    const currentContacts = computed(() => contacts[currentTab.value] || [])

    const getContactImage = (name) => {
      const isMobile = window.innerWidth <= 768
      const suffix = isMobile ? 'mobile' : 'desktop'
      return `/images/contact/${name}-${suffix}.png`
    }

    const copyToClipboard = (text) => {
      if (navigator.clipboard && window.isSecureContext) {
        navigator.clipboard.writeText(text).then(() => {
          alert('Copied to clipboard!')
        }).catch(err => {
          console.error('Failed to copy: ', err)
        })
      } else {
        // Fallback for older browsers
        const textArea = document.createElement("textarea")
        textArea.value = text
        document.body.appendChild(textArea)
        textArea.focus()
        textArea.select()
        try {
          document.execCommand('copy')
          alert('Copied to clipboard!')
        } catch (err) {
          console.error('Failed to copy: ', err)
        }
        document.body.removeChild(textArea)
      }
    }

    return {
      currentTab,
      currentView,
      tabs,
      viewOptions,
      currentContacts,
      getContactImage,
      copyToClipboard
    }
  }
}
</script>

