<!-- src/views/Books.vue -->
<template>
  <div class="books content-container">
    <h1 class="books-title">Books</h1>
    <div class="tabs-container">
      <button 
        v-for="tab in tabs" 
        :key="tab.key"
        @click="currentTab = tab.key"
        :class="['tab-button', { active: currentTab === tab.key }]"
      >
        {{ tab.label }}
      </button>
    </div>
    <BookTab
      :tab="currentTab"
      :books="filteredBooks"
      @navigate-to-book="navigateToBookDetails"
    />
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import BookTab from '@/components/BookTab.vue'

export default {
  name: 'Books',
  components: { BookTab },
  setup() {
    const store = useStore()
    const currentTab = ref('reading')

    const tabs = [
      { key: 'reading', label: 'Reading' },
      { key: 'read', label: 'Read' },
      { key: 'to-read', label: 'To Read' }
    ]

    onMounted(() => {
      store.dispatch('content/loadBooks')
    })

    const books = computed(() => store.getters['content/getBooks'])

    const filteredBooks = computed(() => {
      switch (currentTab.value) {
        case 'reading':
          return books.value.filter(book => book.tags.includes('currently-reading'))
        case 'read':
          return books.value.filter(book => 
            !book.tags.includes('currently-reading') && 
            !book.tags.includes('to-read') && 
            !book.tags.includes('wtr')
          )
        case 'to-read':
          return books.value.filter(book => 
            book.tags.includes('to-read') || book.tags.includes('wtr')
          )
        default:
          return []
      }
    })

    const navigateToBookDetails = (isbn) => {
      console.log(`Navigating to book details for ISBN: ${isbn}`)
    }

    return {
      currentTab,
      tabs,
      filteredBooks,
      navigateToBookDetails
    }
  }
}
</script>

<style scoped>
.books {
  padding: var(--spacing-medium);
}

.books-title {
  margin-bottom: var(--spacing-large);
}

.tabs-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--spacing-medium);

  overflow: hidden;
}


.tab-button {
  flex: 1;
  padding: var(--spacing-small) var(--spacing-medium);
  background: none;
  border: none;
  cursor: pointer;
  font-size: var(--font-size-normal);
  color: var(--text-color);
  transition: all 0.3s ease;
  position: relative;
}

.tab-button.active {
  color: var(--highlight-color);
  font-weight: bold;
}

.tab-button.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: var(--highlight-color);
}

@media (max-width: 768px) {
  .tabs-container {
    flex-direction: row;
  }

  .tab-button {
    padding: var(--spacing-small) var(--spacing-small);
  }
}
</style>
