// src/main.js
import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { 
  faStar, faDumbbell, faFistRaised, faWeightHanging, faRunning, faBurn, 
  faChild, faBolt, faBrain, faSquareRootAlt, faSuperscript, faRobot, 
  faNetworkWired, faProjectDiagram, faUsers, faComments, faUserTie,
  faChevronUp, faChevronDown, faUserCircle, faEye, faBars, faBook, 
  faPen, faFlask, faEnvelope, faMoon, faSun, faHeart, faChartLine, 
  faSearch, faHandPaper, faBalanceScale, faBullseye, faHammer, 
  faHeartbeat, faShieldAlt, faGraduationCap, faLightbulb, faLeaf
} from '@fortawesome/free-solid-svg-icons'

library.add(
  faStar, faDumbbell, faFistRaised, faWeightHanging, faRunning, faBurn, 
  faChild, faBolt, faBrain, faSquareRootAlt, faSuperscript, faRobot, 
  faNetworkWired, faProjectDiagram, faUsers, faComments, faUserTie,
  faChevronUp, faChevronDown, faUserCircle, faEye, faBars, faBook, 
  faPen, faFlask, faEnvelope, faMoon, faSun, faHeart, faChartLine, 
  faSearch, faHandPaper, faBalanceScale, faBullseye, faHammer, 
  faHeartbeat, faShieldAlt, faGraduationCap, faLightbulb, faLeaf
)

const app = createApp(App)

app.component('font-awesome-icon', FontAwesomeIcon)
app.use(router)
app.use(store)

app.mount('#app')
