<!-- src/components/BookItem.vue -->
<template>
  <div class="book-card" :class="currentView">
    <template v-if="currentView === 'compact'">
      <div class="compact-content">
        <p class="book-title">{{ truncateTitle(book.displayName || book.title) }}</p>
        <p class="book-author">{{ book.author }}</p>
      </div>
      <div v-if="book.progress > 0 && book.progress < 100" class="progress-container">
        <div class="progress-bar">
          <div class="progress" :data-progress="book.progress" style="width: 0%"></div>
        </div>
        <span class="progress-text">{{ book.progress }}%</span>
      </div>
    </template>
    
    <template v-else-if="currentView === 'list'">
      <div class="book-cover">
        <img v-if="book.image" :src="book.image" :alt="book.displayName + ' cover'" class="book-cover">
        <div v-else class="book-cover-placeholder">
          <i class="fas fa-book"></i>
        </div>
      </div>
      <div class="book-details">
        <div class="book-title">{{ book.displayName || book.title }}</div>
        <div class="book-author">{{ book.author }}</div>
        <div v-if="book.dateRead" class="completion-date">
          Completed on: {{ formatDate(book.dateRead) }}
        </div>
        <div v-if="book.progress > 0 && book.progress < 100" class="progress-container">
          <div class="progress-bar">
            <div class="progress" :data-progress="book.progress" style="width: 0%"></div>
          </div>
          <span class="progress-text">{{ book.progress }}%</span>
        </div>
      </div>
    </template>

    <template v-else>
      <div class="book-cover">
        <img v-if="book.image" :src="book.image" :alt="book.displayName + ' cover'" class="book-cover">
        <div v-else class="book-cover-placeholder">
          <i class="fas fa-book"></i>
        </div>
      </div>
      <div v-if="book.progress > 0 && book.progress < 100" class="progress-container">
        <div class="progress-bar">
          <div class="progress" :data-progress="book.progress" style="width: 0%"></div>
        </div>
        <span class="progress-text">{{ book.progress }}%</span>
      </div>
    </template>
  </div>
</template>

<script>
import { onMounted } from 'vue'

export default {
  name: 'BookItem',
  props: {
    book: {
      type: Object,
      required: true
    },
    currentView: {
      type: String,
      default: 'compact'
    }
  },
  setup() {
    onMounted(() => {
      setTimeout(() => {
        document.querySelectorAll('.progress').forEach(el => {
          el.style.transition = 'none';
          el.style.width = '0%';
          el.offsetHeight; // Force a reflow
          el.style.transition = 'width 1s ease-out';
          el.style.width = `${el.dataset.progress}%`;
        });
      }, 100);
    });
  },
  methods: {
    formatDate(dateString) {
      if (!dateString) return 'Not available'
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    },
    truncateTitle(title) {
      return title.length > 40 ? title.substring(0, 37) + '...' : title
    }
  }
}
</script>

