// src/store/modules/skills.js
import intelligenceSkills from '@/assets/skills_intelligence.json';
import dexteritySkills from '@/assets/skills_dexterity.json';
import speechcraftSkills from '@/assets/skills_speechcraft.json';
import strengthSkills from '@/assets/skills_strength.json';
import wisdomSkills from '@/assets/skills_wisdom.json';

const state = {
  skillTrees: {
    intelligence: null,
    dexterity: null,
    speechcraft: null,
    strength: null,
    wisdom: null
  },
  currentSkillTree: 'intelligence'
};

const mutations = {
  SET_SKILL_TREE(state, { category, skillTree }) {
    state.skillTrees[category] = skillTree;
  },
  SET_CURRENT_SKILL_TREE(state, category) {
    state.currentSkillTree = category;
  },
  TOGGLE_NODE(state, { category, nodeId }) {
    const toggleNode = (node) => {
      if (node.id === nodeId) {
        node.expanded = !node.expanded;
        return true;
      }
      if (node.children) {
        return node.children.some(toggleNode);
      }
      return false;
    };
    if (state.skillTrees[category]) {
      toggleNode(state.skillTrees[category]);
    }
  },
  TOGGLE_SIBLINGS(state, { category, nodeId }) {
    const toggleSiblings = (node) => {
      if (node.children) {
        const expandedChild = node.children.find(child => child.id === nodeId);
        if (expandedChild) {
          node.children.forEach(child => {
            child.expanded = child.id === nodeId;
          });
          return true;
        }
        return node.children.some(toggleSiblings);
      }
      return false;
    };
    if (state.skillTrees[category]) {
      toggleSiblings(state.skillTrees[category]);
    }
  }
};

const actions = {
  loadSkillTrees({ commit }) {
    const addParentId = (node, parentId = null) => {
      node.parentId = parentId;
      if (node.children) {
        node.children.forEach(child => addParentId(child, node.id));
      }
      return node;
    };
    
    const skillTrees = {
      intelligence: addParentId(JSON.parse(JSON.stringify(intelligenceSkills))),
      dexterity: addParentId(JSON.parse(JSON.stringify(dexteritySkills))),
      speechcraft: addParentId(JSON.parse(JSON.stringify(speechcraftSkills))),
      strength: addParentId(JSON.parse(JSON.stringify(strengthSkills))),
      wisdom: addParentId(JSON.parse(JSON.stringify(wisdomSkills)))
    };

    Object.entries(skillTrees).forEach(([category, skillTree]) => {
      commit('SET_SKILL_TREE', { category, skillTree });
    });
  },
  setCurrentSkillTree({ commit }, category) {
    commit('SET_CURRENT_SKILL_TREE', category);
  },
  toggleNode({ commit, state }, nodeId) {
    commit('TOGGLE_NODE', { category: state.currentSkillTree, nodeId });
  },
  toggleSiblings({ commit, state }, nodeId) {
    commit('TOGGLE_SIBLINGS', { category: state.currentSkillTree, nodeId });
  }
};

const getters = {
  getCurrentSkillTree: (state) => state.skillTrees[state.currentSkillTree],
  getAllSkillTrees: (state) => state.skillTrees,
  getCurrentSkillTreeCategory: (state) => state.currentSkillTree
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
};
