<!-- src/components/BookTab.vue -->
<template>

  <div class="book-tab">
    <div class="controls">
      <div class="sort-options">
        <span>Sort:</span>
        <button 
          v-for="option in sortOptions" 
          :key="option.value"
          @click="currentSort = option.value"
          :class="{ active: currentSort === option.value }"
        >
          <i :class="['fas', option.icon]"></i> {{ option.label }}
        </button>
      </div>
      <div class="view-toggle">
        <span>View:</span>
        <button 
          v-for="view in viewOptions" 
          :key="view.value"
          @click="currentView = view.value"
          :class="{ active: currentView === view.value }"
        >
          <i :class="['fas', view.icon]"></i>
        </button>
      </div>
    </div>

    <template v-if="tab === 'read'">
      <h2>Non-Fiction</h2>
      <div :class="['book-list', currentView]">
        <BookItem
          v-for="book in sortedNonFictionBooks"
          :key="getUniqueKey(book)"
          :book="book"
          :currentView="currentView"
          @click="$emit('navigate-to-book', book.isbn)"
        />
      </div>
      <h2>Fiction</h2>
      <div :class="['book-list', currentView]">
        <BookItem
          v-for="book in sortedFictionBooks"
          :key="getUniqueKey(book)"
          :book="book"
          :currentView="currentView"
          @click="$emit('navigate-to-book', book.isbn)"
        />
      </div>
      <h2>Comics</h2>
      <div :class="['book-list', currentView]">
        <BookItem
          v-for="book in sortedComicsBooks"
          :key="getUniqueKey(book)"
          :book="book"
          :currentView="currentView"
          @click="$emit('navigate-to-book', book.isbn)"
        />
      </div>
    </template>
    
    <template v-else>
      <div :class="['book-list', currentView]">
        <BookItem
          v-for="book in sortedBooks"
          :key="getUniqueKey(book)"
          :book="book"
          :currentView="currentView"
          @click="$emit('navigate-to-book', book.isbn)"
        />
      </div>
    </template>
  </div>
</template>




<script>
import { ref, computed } from 'vue'
import BookItem from '@/components/BookItem.vue'

export default {
  name: 'BookTab',
  components: { BookItem },
  props: {
    tab: {
      type: String,
      required: true
    },
    books: {
      type: Array,
      required: true
    }
  },
  emits: ['navigate-to-book'],
  setup(props) {
    const currentSort = ref('title')
    const currentView = ref('list')

    const sortOptions = computed(() => {
      const options = [
        { value: 'title', label: 'Title', icon: 'fa-sort-alpha-down' },
        { value: 'author', label: 'Author', icon: 'fa-user' }
      ]
      if (props.tab === 'reading' || props.tab === 'read') {
        options.push({ value: 'progress', label: 'Progress', icon: 'fa-tasks' })
      }
      if (props.tab === 'read') {
        options.push({ value: 'dateRead', label: 'Date Read', icon: 'fa-calendar-alt' })
      }
      return options
    })

    const viewOptions = [
      { value: 'compact', icon: 'fa-bars' },
      { value: 'list', icon: 'fa-list' },
      { value: 'grid', icon: 'fa-th-large' }
    ]
    const fictionTags = ['plays', 'graphic-novels', 'novels']
    const comicsTags = ['manga', 'manwha', 'western-comic', 'young-comics', 'young-novels']

    const sortBooks = (books) => {
      return [...books].sort((a, b) => {
        switch (currentSort.value) {
          case 'title':
            return (a.displayName || a.title).localeCompare(b.displayName || b.title)
          case 'author':
            return a.author.localeCompare(b.author)
          case 'progress':
            return (b.progress || 0) - (a.progress || 0)
          case 'dateRead':
            return new Date(b.dateRead || 0) - new Date(a.dateRead || 0)
          default:
            return 0
        }
      })
    }

    const sortedBooks = computed(() => sortBooks(props.books))

    const sortedNonFictionBooks = computed(() => {
      const nonFictionBooks = props.books.filter(book => 
        !book.tags.some(tag => [...fictionTags, ...comicsTags].includes(tag))
      )
      return sortBooks(nonFictionBooks)
    })

    const sortedFictionBooks = computed(() => {
      const fictionBooks = props.books.filter(book => 
        book.tags.some(tag => fictionTags.includes(tag))
      )
      return sortBooks(fictionBooks)
    })

    const sortedComicsBooks = computed(() => {
      const comicsBooks = props.books.filter(book => 
        book.tags.some(tag => comicsTags.includes(tag))
      )
      return sortBooks(comicsBooks)
    })

    const getUniqueKey = (book) => {
      return book.isbn || `${book.title}-${book.author}`
    }

    return {
      currentSort,
      currentView,
      sortOptions,
      viewOptions,
      sortedBooks,
      sortedNonFictionBooks,
      sortedFictionBooks,
      sortedComicsBooks,
      fictionTags,
      comicsTags,
      getUniqueKey
    }
  }
}
</script>

<style scoped>

.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-medium);
}

.sort-options, .view-toggle {
  display: flex;
  align-items: center;
}

.sort-options span, .view-toggle span {
  margin-right: var(--spacing-small);
}

.sort-options button, .view-toggle button {
  background: none;
  border: none;
  color: var(--text-color);
  font-size: var(--font-size-small);
  cursor: pointer;
  margin-left: var(--spacing-small);
  padding: var(--spacing-small);
}

.sort-options button.active, .view-toggle button.active {
  color: var(--highlight-color);
  font-weight: bold;
}

h2 {
  margin-top: var(--spacing-large);
  margin-bottom: var(--spacing-medium);
  color: var(--highlight-color);
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-small);
  margin-bottom: var(--spacing-medium);
}

.tag {
  background-color: var(--highlight-color);
  color: var(--background-color);
  padding: 4px 8px;
  border-radius: 4px;
  font-size: var(--font-size-small);
}

.book-list {
  margin-bottom: var(--spacing-large);
}

.book-list.list {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-medium);
}

.book-list.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: var(--spacing-large);
}

.book-list.compact {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-small);
}







@media (max-width: 768px) {
  .controls {
    flex-direction: column;
    align-items: flex-start;
  }

  .sort-options {
    margin-bottom: var(--spacing-small);
  }

  .book-list.grid {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  }
}

</style>
